<template>
  <div class="address-container">
    <van-cell center icon="location" label="上海市闵行区上海康城小区城道80号503室">
      <template #title>
        <span class="custom-title">张杰医师</span>
        <span class="phone">127400274930</span>
      </template>
      <template #right-icon>
        <div class="right_text">编辑</div>
      </template>
    </van-cell>
    <div class="footer">
      <button type="submit" @click.prevent="path('addressCreate')">添加新地址</button>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {
    path(name) {
      this.$router.push({ name });
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.address-container {
  min-height: 100vh;
  background-color: #eeeeee;
  padding: 15px 0 65px;
  .van-cell {
    margin-bottom: 15px;
    padding: 10px 12px;
    i:first-child {
      width: 25px;
      height: 25px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: #c8a060;
      color: #fff;
      font-size: 12px;
    }
    .van-cell__left-icon {
      margin-right: 12px;
    }
    .van-cell__title {
      .custom-title {
        font-size: 15px;
        color: #000000;
        margin-right: 10px;
      }
      .phone {
        font-size: 12px;
        color: #666666;
      }
      .van-cell__label {
        font-size: 13px;
        color: #000000;
      }
      .orders_id {
        font-size: 12px;
        color: #000000;
      }
    }
  }
  .right_text {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding-left: 12px;
    height: 30px;
    color: #000;
    border-left: 1px solid #999999;
  }
}
</style>


